body {
  padding: 0;
  margin: 0 0;
  font-family: sans-serif;
}
.wrapper {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 25px 0 50px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
}

.navbar li {
    display: inline-block;
    text-align: center;
  }
  
  /* .clickyLeft {
    text-align: left;
    padding: 20px;
  } */
  
  .clickyMiddle {
    text-align: center;
  }
  
  .clickyScoreRight {
    text-align: right;
  }
  
  .navbar {
    background: #051149;
  }
  
  li {
    color: white;
    padding: 15px 0;
  }
  
  body {
    font-size: 24px;
  }
.header{
	background-color: #ccc;
	position: relative;
	width: 100%;
}

h1{
	text-align: center;
	font-size: 90px;
	font-weight: bold;
	padding: 90px 0 20px 0; 
}

h2{
	text-align: center;
	font-size: 25px;
	padding-bottom: 40px;
}
.card {
    width: 190px;
    height: 190px;
    margin: 15px 45px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    border: 3px solid white;
  }

  img {
    width: 190px;
    height: 190px; 
  }

